/* Header styles */

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid lightgray;
  width: 100vw;
  height: 85px;
  padding: 6px 26px;
  margin-right: 30px;
}

.titleAndLogoContainer {
  margin-left: 20px;
  text-decoration: none;
  color: black;
  font-size: 1.3rem;
}

a h1 {
  color: rgb(41, 41, 41);
  transition: color 0.04s linear;
}

a h1:hover {
  color: black;
}

/* NavBar.js styles */

ul {
  display: flex;
  gap: 20px;
  list-style: none;
  align-items: center;
}

.navLink:hover {
  color: black;
  border-bottom: 2px solid black;
}

.navLink3:hover {
  color: black;
  border-bottom: 2px solid black;
}

.navLink {
  color: rgb(41, 41, 41);
  font-size: 1.3rem;
  transition: all 0.04s linear;
}

.navLink3 {
  color: rgb(41, 41, 41);
  font-size: 1.2rem;
  display: inline-block;
  position: relative;
  height: 41px;
  transition: all 0.04s linear;
}

.cartLength {
  position: absolute;
  top: -20%;
  left: 70%;
  font-size: 0.9rem;
}

.checkoutLinkContainer {
  position: relative;
}

/* Quantity incrementer styles */

.quantityIncrementerContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  border: 1px solid grey;
  background-color: white;
  width: 160px;
  padding: 4px;
}

.quantityChangeBtn {
  width: 30px;
  background-color: grey;
  border: none;
  padding: 4px;
}

input[type="number"] {
  width: 40%;
  flex: 1;
  border: none;
  text-align: center;
}

input::placeholder {
  color: rgb(218, 218, 218);
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* BackToShopBtn.js styles */

.backToShopBtn {
  height: 40px;
  background-color: rgb(255, 255, 255);
  border: 1px solid rgb(185, 185, 185);
  border-radius: 8px;
  padding: 12px;
  align-self: flex-start;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 145px;
  color: #292929;
  transition: all 0.1s linear;
}

.backToShopBtn:hover {
  color: black;
  border: 1px solid rgb(137, 137, 137);
}

.backToShopBtn:active {
  color: white;
  background-color: black;
}

/* AddCartButton.js styles */

.addToCartBtn {
  background-color: #6f6f6f;
  border-radius: 8px;
  color: #ebebeb;
  font-size: 1.2rem;
  border: none;
  width: 35%;
  padding: 10px;
  box-shadow: 0px 2px 3px #76767669;
  margin-bottom: 12px;
  transition: background-color 0.1s linear, color 0.1s linear;
  width: 130px;
}

.addToCartBtn:hover {
  background-color: #525252;
  color: white;
}

.addToCartBtn:active {
  color: #525252;
  background-color: #ebebeb;
}

/* CheckOutBtn.js */

.checkOutBtn {
  height: 45px;
  width: 50px;
  background-color: rgb(39, 94, 28);
  box-shadow: 0px 2px 3px #76767669;
  border: none;
  border-radius: 8px;
  padding: 12px;
  align-self: flex-start;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 130px;
  font-size: 1.1rem;
  color: rgb(255, 255, 255);
  align-self: center;
  transition: background-color 0.1s linear, color 0.1s linear;
  flex: 0 0 auto;
}

.checkOutBtn:hover {
  background-color: rgb(31, 77, 22);
}

.checkOutBtn:active {
  background-color: white;
  color: rgb(31, 77, 22);
}

/* Icon styles */

.shoppingCart {
  width: 35px;
  height: 35px;
  color: rgb(41, 41, 41);
  transition: color 0.1s linear;
}

.shoppingCart:hover {
  color: black;
}